[data-nav-layout="horizontal"] {
  @media (min-width: 992px) {
    .sidemenu-layout-styles {
      display: none;
    }
    .main-menu {
      &-container {
        display: inline-flex;
      }
      display: flex !important;
      transition: all 0.5s ease;
    }
    .side-menu__label .badge {
      display: none;
    }
    .mega-menu {
      columns: 3;
      -webkit-columns: 3;
      -moz-columns: 3;
    }
    .app-header {
      padding-inline-start: 0 !important;
      z-index: 104;
      box-shadow: none;
    }
    .app-content {
      min-height: calc(100vh - 10.5rem);
      margin-block-start: 7.3rem;
      margin-inline-start: 0rem !important;
      padding: 0;
      > .container-fluid {
        width: 85%;
        margin-inline-start: auto;
        margin-inline-end: auto;
      }
    }
    .app-sidebar {
      .main-sidebar,
      .simplebar-mask {
        overflow: visible;
      }
      .main-menu > .slide {
        margin: 0 0.1875rem;
      }
      .main-sidebar {
        box-shadow: none;
      }
    }
    .main-sidebar .simplebar-vertical {
      visibility: hidden !important;
    }
    .main-sidebar,
    .main-header-container {
      width: 85%;
      margin: 0 auto;
    }
    .horizontal-logo {
      display: block;
      margin:auto 10px auto auto;
      @media (min-width: 992px) {
        .header-logo {
          img {
            height: 2rem;
            line-height: 2rem;
          }
          .desktop-logo {
            display: block;
          }
          .desktop-dark,
          .toggle-logo,
          .toggle-dark,
          .desktop-white,
          .toggle-white {
            display: none;
          }
        }
      }
    }
    .main-header-container .sidemenu-toggle {
      display: none;
    }
    .app-sidebar {
      width: 100% !important;
      height: auto;
      inset-block-start: 4rem;
      border-inline-end: 0;
      border-block-end: 1px solid $menu-border-color;
      .slide-menu.child1,
      .slide-menu.child2,
      .slide-menu.child3 {
        border: 1px solid $menu-border-color;
        li {
          padding-inline-start: 0.5rem;
        }
        .side-menu__item:before {
          inset-block-start: 1rem;
        }
      }
      .simplebar-content {
        padding: 0 !important;
        overflow: hidden;
      }
      .simplebar-content-wrapper {
        overflow: visible !important;
        height: auto;
      }
      .simplebar-wrapper {
        width: 100%;
      }
      .main-sidebar {
        padding: 0;
      }
      .simplebar-offset {
        position: inherit;
      }
      .slide.has-sub .slide-menu {
        &.child1 {
          inset-inline-start: 1.5rem !important;
          padding: 0.1875rem 0;
        }
        &.child2,
        &.child3 {
          inset-inline-end: 100% !important;
          padding: 0.1875rem 0;
        }
        &.active {
          inset-inline-start: auto !important;
          inset-inline-end: auto !important;
        }
      }
      .slide-menu.child1 {
        position: absolute !important;
      }
      .side-menu__item {
        width: 100%;
        display: flex;
        border-radius: 0;
        padding: 0.92rem;
      }
      .side-menu__angle {
        inset-inline-end: 0.45rem;
        display: block;
      }
      .side-menu__icon {
        margin-inline-end: 0.25rem;
        margin-inline-start: 0;
        margin-block-end: 0;
      }
      .slide.has-sub .slide-menu.child1 {
        background-color: var(--menu-bg);
        inset-block-start: 100% !important;
        box-shadow: 0.063rem 0.188rem 0.5rem $black-1;
        min-width: 12rem;
      }
      .slide {
        padding: 0;
        margin: 0;
      }
      .slide-menu {
        &.child1 {
          border-radius: 0 0 0.25rem 0.25rem;
          .slide.has-sub,
          .slide {
            width: 100%;
            display: flex;
            padding: 0 0.1875rem;
          }
        }
        &.child2,
        &.child3 {
          border-radius: 0.25rem;
        }
      }
      .slide,
      .slide.has-sub {
        position: static;
      }
      .main-menu {
        margin-block-end: 0;
        padding: 0;
      }
    }
    .main-sidebar-header {
      display: none !important;
    }
    .main-sidebar {
      margin-block-start: 0;
      padding-block-end: 3rem;
    }
    .slide__category {
      display: none;
    }
    .main-menu-container .slide-left {
      inset-inline-start: 1.438rem;
    }
    .main-menu-container .slide-left,
    .main-menu-container .slide-right {
      position: absolute;
      top: 0.563rem;
      padding: 0.375rem;
      color: $custom-white;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 1;
      cursor: pointer;
      border: 1px solid $default-border;
      border-radius: 3.125rem;
    }

    .main-menu-container .slide-left,
    .main-menu-container .slide-right {
      position: absolute;
      top: 0.5rem;
      padding: 0.375rem;
      color: $custom-white;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 1;
      cursor: pointer;
      border: 1px solid $default-border;
      border-radius: 3.125rem;
      background: $custom-white;
    }

    .main-menu-container {
      .slide-right {
        inset-inline-end: -2%;
      }
      .slide-left {
        inset-inline-start: -2%;
      }
    }
    &[page-style="classic"] {
      .app-sidebar {
        border-block-end: 1px solid $default-border;
      }
    }
    &[dir="rtl"] {
      .main-menu-container {
        .slide-right,
        .slide-left {
          transform: rotate(180deg);
        }
      }
    }
    .app-sidebar__user,
    .app-sidebar-help {
      display: none;
    }
    .main-header-container .header-content-right .header-link {
      margin: 0px 10px;
    }
  }
}
