}
.lds-heart div:after {
  content: " ";
  position: absolute;
  display: block;
  width: 26px;
  height: 26px;
  background: var(--primary-color);
  top: -17px;
  border-radius: 50% 50% 0 0;
}
.lds-heart div:before {
  content: " ";
  position: absolute;
  display: block;
  width: 26px;
  height: 26px;
  background: var(--primary-color);
  inset-inline-start: -17px;
  border-radius: 50% 0 0 50%;
}

@keyframes lds-heart {
  0% {
    transform: scale(0.95);
  }
  5% {
    transform: scale(1.1);
  }
  39% {
    transform: scale(0.85);
  }
  45% {
    transform: scale(1);
  }
  60% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(0.9);
  }
}
.lds-ring {
  position: relative;
  width: 64px;
  height: 64px;
  margin: 100px auto;
}
.lds-ring div {
