  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.lds-hourglass {
  position: relative;
  width: 40px;
  height: 40px;
  margin: 100px auto;
}
.lds-hourglass:after {
  content: " ";
